// Translator.vue
<template>
  <div id="app">
    <h1>Translator App</h1>
    <textarea v-model="input" v-on:keyup.enter = 'translate' id='translation' placeholder = "What would you like to translate?" style = 'height : 300px; width : 500px'>
    </textarea>
    <div id="button" style='text-allign : center'>
      <button v-on:click = 'translate' style = 'height : 100; width : 200px; position : center'  > Translate </button> 
    </div>
    <p> The message is : {{ output }} </p>
  </div>
</template>

<script>


export default {
  name: 'App',
  data() {
    return {
      output : 'test',
      input : '',
      input_language : 'nl',
      output_language : 'fr'
    }
  },
  methods: {
    translate: function(input_language,output_language){
        return this.output = this.input,
        this.input = ''
       }
 }

}
</script>

<style>
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  body {
    font-family: Arial, Helvetica, sans-serif;
    line-height: 1.4;
  }
</style>





